@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");

.list__item {
  display: flex;
  flex-flow: row;
  align-items: center;
  margin-bottom: 1rem;
  cursor: pointer;
}

.list__detail {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  margin-left: 0.5rem;
}
.list__detail > h5 {
  margin: 0;
}

.list__title {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 1rem;
}

.list__creator,
.list__metrics {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 0.9rem;
  color: rgb(96, 96, 96);
}
