@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.profile__img {
  border-radius: 50%;
  margin: 0 1rem 0 0;
}

.icon__svg {
  width: 1.5rem;
  margin: 0 1rem 0 1rem;
}

.icon__svg:hover {
  cursor: pointer;
}
.icon__logo:hover {
  cursor: pointer;
}

.icon__logo {
  width: 5rem;
}

.logo {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row;
          flex-flow: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: start;
          justify-content: start;
}

.search {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row;
          flex-flow: row;
  -webkit-justify-content: start;
          justify-content: start;
  -webkit-align-items: stretch;
          align-items: stretch;
  margin-left: 1rem;
  -webkit-flex: 1 1;
          flex: 1 1;
  max-width: 45rem;
}

.search__input {
  padding: 0 0 0 0.5rem;
  box-sizing: border-box;
  -webkit-flex: 1 1;
          flex: 1 1;
}

#embedIcon {
  width: 1rem;
}

.nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: start;
          justify-content: start;
  -webkit-flex-flow: row;
          flex-flow: row;
  -webkit-align-items: center;
          align-items: center;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-flow: row;
          flex-flow: row;
  grid-area: Header;
  margin-bottom: 2rem;
}

.container {
  display: grid;
  grid-template-areas: "Header Header" "VideoDetail VideoList";
  grid-template-columns: 5fr 2fr;
}

.video__playback {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.video__content {
  position: relative;
  width: 100%;
  max-width: 67vw;
  padding-bottom: 56.25%;
}

.video__description h2 {
  font-family: "Roboto", sans-serif;
  font-size: 1.5rem;
  font-weight: 400;
}

.video__description p {
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: 300;
}

.video {
  grid-area: VideoDetail;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-align-items: stretch;
          align-items: stretch;
  margin-left: 4vw;
}

.list__item {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row;
          flex-flow: row;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 1rem;
  cursor: pointer;
}

.list__detail {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  margin-left: 0.5rem;
}
.list__detail > h5 {
  margin: 0;
}

.list__title {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 1rem;
}

.list__creator,
.list__metrics {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 0.9rem;
  color: rgb(96, 96, 96);
}

.list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  margin-left: 1.5rem;
}

